.onlineBlock {
    padding: 50px 0;
    background-color: #fffeec;
}
.onlineBlockContent{
    text-align: center;
    border: 1px solid;
}
.onlineBlockContent h3{
    margin-bottom: 50px;
}
.onlineBlockContent button{
    cursor: pointer;
    border: 1px solid;
    border-radius: 5px;
    padding: 5px 10px;
}